                    class="d-flex child-flex"
                    @click="showImage(stageGroupIdx, stageIndex)"
                  >
                    <Entry
                      :previewPath="previewPathFromStage(stageIndex, stageGroup[stageIndex - 1], stageGroupIdx)"
                      :downloadPath="downloadPathFromStage(stageIndex, stageGroup[stageIndex - 1])"
                      :avatarUrl="avatarUrlFromStage(stageGroup[stageIndex - 1])"
                    />
                  </v-col>
                </v-row>
              </v-container>
            </v-card>
          </v-col>
        </v-row>
      </v-container>
    </v-content>
    <LightBox :media="lightboxMedia" :showLightBox="false" :showCaption="true" ref="lightbox">
      <template v-slot:customCaption>
        <v-row class="lightbox-caption px-4">
          <v-col align="left" class="title font-weight-black">
            <v-avatar color="blue-grey darken-4 mr-2">
              <img :src="avatarUrlFromStage(selectedParticipant.name)">
            </v-avatar>
            {{selectedParticipant.displayName}}
          </v-col>
          <v-col class="h5 font-weight-black social-container">
            <v-btn v-if="selectedParticipant.twitter" class="mr-2" fab small color="light-blue lighten-1" :href="selectedParticipant.twitter">
              <v-icon>fa-twitter</v-icon>
            </v-btn>
            <v-btn v-if="selectedParticipant.deviantart" class="mr-2" fab small color="green" :href="selectedParticipant.deviantart">
              <v-icon>fa-deviantart</v-icon>
            </v-btn>
            <v-btn v-if="selectedParticipant.instagram" class="mr-2" fab small color="purple darken-2" :href="selectedParticipant.instagram">
              <v-icon>fa-instagram</v-icon>
            </v-btn>
            <v-btn v-if="selectedParticipant.tumblr" class="mr-2" fab small color="indigo darken-4" :href="selectedParticipant.tumblr">
              <v-icon>fa-tumblr</v-icon>
            </v-btn>
            <v-btn v-if="selectedParticipant.facebook" class="mr-2" fab small color="blue darken-4" :href="selectedParticipant.tumblr">
              <v-icon>fa-facebook</v-icon>
            </v-btn>
            <v-btn v-if="selectedParticipant.artstation" class="mr-2" fab small color="light-blue accent-3" :href="selectedParticipant.artstation">
              <img :src="require('./assets/artstation-logo.png')" width="24">
            </v-btn>
            <v-btn v-if="selectedParticipant.youtube" class="mr-2" fab small color="red" :href="selectedParticipant.youtube">
              <v-icon>fa-youtube</v-icon>
            </v-btn>
          </v-col>
        </v-row>
      </template>
    </LightBox>
  </v-app>
</template>

<script>
