  name: 'App',
  components: {
    Entry,
    LightBox
  },

  data: () => ({
    participants: {
      '4sonicfan': {
        deviantart: 'https://www.deviantart.com/4sonicfan',
        instagram: 'https://www.instagram.com/phyllis_artpage',
