      },
      'ArmorGontier': {
        deviantart: null,
        twitter: null,
      },
      'AussieHobbyYeah': {
        deviantart: null,
        twitter: null,
      },
      'Clever The Wolf': {
        alias: 'Clever the Wolf',
        twitter: 'https://twitter.com/CygnusCaptain',
      },
      'Flamestream': {
        deviantart: 'https://deviantart.com/sonicolas',
        twitter: 'https://twitter.com/flamestream',
        tumblr: 'https://firesquall.tumblr.com/',
        youtube: 'https://www.youtube.com/channel/UCMChlbT9akLoztRcN3Xs-gg',
        artstation: 'https://www.artstation.com/flamestream',
      },
      'Flappy': {
        instagram: 'https://www.instagram.com/flappchen/',
        twitter: 'https://twitter.com/flappchen_',
      },
      'Goodie_Noodles (Nichan)': {
        alias: 'Nichan',
        deviantart: 'https://www.deviantart.com/xanime-sonicartistx',
        twitter: 'https://twitter.com/Nichan_Artz',
        instagram: 'https://www.instagram.com/0o_nichan_artz_o0',
      },
      'Mllermanda': {
        deviantart: 'https://www.deviantart.com/mllermanda',
        tumblr: 'https://mllermanda.tumblr.com',
      },
      'Nini Rosé': {
        alias: 'Shad-Kun',
        twitter: 'https://twitter.com/shad_kun',
      },
      'NOOB': {
        alias: 'Noviceartist89',
        deviantart: 'https://www.deviantart.com/noviceartist89',
        twitter: 'https://twitter.com/Noviceartist89',
      },
      'Rotten': {
        deviantart: 'https://www.deviantart.com/minimalistichipster',
        twitter: 'https://twitter.com/MakerRotten',
      },
      'TylerBucket': {
        deviantart: 'https://www.deviantart.com/tylerbucket',
        twitter: 'https://twitter.com/itstylerbucket',
        youtube: 'https://www.youtube.com/channel/UCVQd4Dy1u_dW5nD4MWmEBOw',
      },
      'Wolfie': {
        deviantart: null,
        twitter: null,
      },
      'spellofthedead': {
        alias: 'Spellofthedead',
        deviantart: 'https://www.deviantart.com/spellofthedead',
        twitter: 'https://www.instagram.com/spellofthedead/',
        youtube: 'https://www.youtube.com/channel/UCX1fxD3vkZhSRTzV-MUSwYw',
      },
    },
    stageGroups: [
      ['4sonicfan', 'Clever The Wolf', 'Rotten', 'Nini Rosé', '4sonicfan'],
      ['ArmorGontier', 'Flamestream-2', 'Goodie_Noodles (Nichan)-2', 'Nini Rosé-2', 'ArmorGontier'],
      ['AussieHobbyYeah', 'NOOB', 'Clever The Wolf', 'Flamestream-2', 'AussieHobbyYeah'],
      ['Clever The Wolf', 'Nini Rosé-2', 'Flamestream', 'AussieHobbyYeah', 'Clever The Wolf'],
      ['Flamestream', 'Mllermanda', 'AussieHobbyYeah', 'Flappy', 'Flamestream'],
      ['Flamestream-2', 'Wolfie', 'Nini Rosé-2', 'spellofthedead', 'Flamestream-2'],
      ['Flappy', 'spellofthedead', 'Mllermanda', 'ArmorGontier', 'Flappy'],
      ['Goodie_Noodles (Nichan)', 'Flappy', 'Nini Rosé', 'spellofthedead-2', 'Goodie_Noodles (Nichan)'],
      ['Goodie_Noodles (Nichan)-2', 'spellofthedead-2', 'TylerBucket', 'Clever The Wolf', 'Goodie_Noodles (Nichan)-2'],
      ['Mllermanda', 'Flamestream', 'ArmorGontier', '4sonicfan', 'Mllermanda'],
      ['Nini Rosé', 'AussieHobbyYeah', 'Flamestream-2', 'Rotten', 'Nini Rosé'],
      ['Nini Rosé-2', 'ArmorGontier', 'spellofthedead-2', 'Wolfie', 'Nini Rosé-2'],
      ['NOOB', 'TylerBucket', 'Flappy', 'Goodie_Noodles (Nichan)-2', 'NOOB'],
      ['Rotten', 'Nini Rosé', '4sonicfan', 'Flamestream', 'Rotten'],
      ['TylerBucket', 'Goodie_Noodles (Nichan)', 'spellofthedead', 'NOOB', 'TylerBucket'],
      ['Wolfie', 'Rotten', 'Goodie_Noodles (Nichan)', 'Mllermanda', 'Wolfie'],
      ['spellofthedead', 'Goodie_Noodles (Nichan)-2', 'Wolfie', 'TylerBucket', 'spellofthedead'],
      ['spellofthedead-2', '4sonicfan', 'NOOB', 'Goodie_Noodles (Nichan)', 'spellofthedead-2'],
    ],
    stageCount: 5,
    selectedStageGroupIndex: 0,
    // @TEMP
    maxLightboxImageCounts: [
      4,
      4,
      4,
      3,
      4,
      4,
      4,
      4,
      4,
      5,
      5,
      3,
      4,
      4,
      5,
      4,
      4,
      4,
    ],
  }),

  computed: {
    displayedIds() {
      return this.maxSelected ? this.selectedIds : Object.keys(this.images);
    },
    lightboxMedia() {
      let { selectedStageGroup } = this;

      // Sanity
      if (!selectedStageGroup) {
        return;
      }

      // TEMP
      selectedStageGroup = this.selectedStageGroup.slice(0, this.maxLightboxImageCounts[this.selectedStageGroupIndex]);

      return selectedStageGroup.map((name, stageIndex) => {
        return {
          thumb: `entries/${this.previewPathFromStage(stageIndex + 1, name)}`,
          src: `entries/${this.previewPathFromStage(stageIndex + 1, name)}`,
          caption: name,
        };
      });
    },
    selectedStageGroup() {
      let { selectedStageGroupIndex } = this;
      return this.stageGroups[selectedStageGroupIndex];
    },
    selectedParticipant() {
      let { selectedStageGroup, participants } = this;

      // Sanity
      if (!selectedStageGroup) {
        return {};
      }

      let participantName = this.participantNameFromStage(selectedStageGroup[this.$refs.lightbox.select]);
      let participantInfo = participants[participantName];
      return {
        ...participantInfo,
        name: participantName,
        displayName: participantInfo.alias || participantName,
      };
    },
  },

  methods: {
    previewPathFromStage(stageIndex, stageName, stageGroupIdx) {
      if (!stageName) return;

      // TEMP
      if (stageIndex > this.maxLightboxImageCounts[stageGroupIdx])
        return;

      return `stage-${stageIndex}/${stageName}.png`
    },
    downloadPathFromStage(stageIndex, stageName) {
      if (stageIndex > 4) return;
      if (!stageName) return;
      return `entries/stage-${stageIndex}/${stageName}.psd`
    },
    participantNameFromStage(stageName) {
      return stageName.match(/(.+?)(-\d+)?$/)[1];
    },
    avatarUrlFromStage(stageName) {
      if (!stageName) return;
      let participantName = this.participantNameFromStage(stageName);
      return require(`./assets/avatars/${participantName}.png`);
    },
    showImage(stageGroupIdx, stageIndex) {
      // TEMP
      if (stageIndex > this.maxLightboxImageCounts[stageGroupIdx])
        return;

      this.selectedStageGroupIndex = stageGroupIdx;
      this.$refs.lightbox.showImage(stageIndex - 1);
    }
  },

  async created () {
    console.log(Object.entries(this.participants).map(([k,v]) => { return v.alias || k }).join(', '));
    this.$vuetify.theme.dark = true
  },
};
</script>

